// Convert to hashtables from our data frame structure
const eventData = {}
for (let i = 0; i < eventJSON["Name"].length; i++) {
  let id = eventJSON["Order"][i]
  eventData[id] = {
    id: eventJSON["Order"][i],
    name: eventJSON["Name"][i],
    description: eventJSON["Description"][i],
  }
}

const crisisData = {}
for (let i = 0; i < crisisJSON["Name"].length; i++) {
  let id = crisisJSON["ID"][i]
  crisisData[id] = {
    id: id,
    name: crisisJSON["Name"][i],
    description: crisisJSON["Description"][i],
  }
}

